*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;cursor:default}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.App{color:#000;display:flex;flex-direction:row;height:100%}@media (max-width: 910px){.App{flex-direction:column;z-index:1}}.side-bar{flex:1 1;min-width:250px;height:100vh;position:-webkit-sticky;position:sticky;top:0;background:#004181}@media (max-width: 910px){.side-bar{position:static;height:auto;flex:auto}}.main-window{flex:4 1;height:100%}@media (max-width: 910px){.main-window{position:static;flex:auto}}

.input{width:100%;background-color:#fff;position:-webkit-sticky;position:sticky;top:0;z-index:2;-webkit-transition:0.2s;transition:0.2s}@media (max-width: 910px){.input{position:static;z-index:1}}.initialInput-table{width:100%}.hidden{display:none}


.inputRowTitle{font-size:1.1rem;color:white;text-align:center;margin-bottom:20px}@media only screen and (max-width: 910px){.inputRowTitle{font-size:1rem;margin-bottom:5px}}

.inputRow{width:100%;height:40px;font-weight:500}.inputRow-label{text-align:left;font-size:1rem}.inputRow-label:hover{color:#c7ea46}@media (max-width: 910px){.inputRow-label{height:30px}}.inputRow-input{text-align:left;padding:0 0 0 10px}.inputRow-input input{width:100%;text-align:center;border:none;border-radius:2px;outline:none;line-height:1.75}.inputRow-input input:hover{cursor:pointer}.inputRow-units{font-size:.7rem}@media (max-width: 910px){.inputRow-label{font-size:.8rem}.inputRow{height:20px}}

.inputTable{margin:auto}


.toggleRow{width:100%;height:40px}.toggleRow-label{text-align:left;font-size:1rem;font-weight:500}.toggleRow-label:hover{color:#c7ea46}@media (max-width: 910px){.toggleRow-label{height:30px}}.toggleRow-input{text-align:left;padding:0 0 0 10px}.toggleRow-button{width:100%;line-height:1.75;border:none;outline:none;background:white}.toggleRow-button:hover{cursor:pointer}.toggleRow-input input{width:100%;text-align:center;border:none;border-radius:2px;outline:none;line-height:1.75}.toggleRow-units{font-size:.7rem}@media only screen and (max-width: 910px){.toggleRow-label{font-size:.8rem}.toggleRow{height:20px}}

.compactView{width:100%;background:#004181;text-align:center;display:flex;justify-content:space-around;padding:2vh 5vh}@media (max-width: 910px){.compactView{flex-direction:column}}.compactView h1{flex:1 1;padding:0 1vh;color:white;margin-bottom:1vh;font-size:1.7vh}.compactView .compactView-emphasis{font-size:3vh;font-weight:500}.compactView .compactView-goalReached{color:orange;font-size:3vh;font-weight:500}

.inputColumn{border-radius:20px;padding:10px 0;background:#004181;color:white}@media (max-width: 910px){.inputColumn{margin:10px 0}}

.weekLabel{background:#fff;position:relative;z-index:2;display:block;border-bottom:1px solid black;justify-content:center}@media (max-width: 910px){.weekLabel{font-size:.7rem}}.weekLabel-wrapper{display:flex;padding:10px 0;text-align:center}.weekLabel-wrapper div{flex:1 1;font-weight:600}@media (max-width: 910px){.noMobile{display:none}}

.inputColumnWrapper{display:flex;justify-content:space-evenly;background-color:#004181}@media (max-width: 910px){.inputColumnWrapper{flex-direction:column;padding:0 10px}}

.middlePanel-wrapper{width:100%;text-align:center;display:flex;padding:10px}.addWeekBtn{font-size:1.1rem;border:1px solid black;padding:10px 10px;border-radius:8px;margin:10px auto;background:#fff;outline:none;box-shadow:0 5px #666;-webkit-transition:0.1s ease-in;transition:0.1s ease-in;font-weight:500}.addWeekBtn:active{box-shadow:0 2px #666;-webkit-transform:translateY(4px);transform:translateY(4px)}.addWeekBtn:hover{background-color:#fff}@media (max-width: 910px){.addWeekBtn{width:50%;margin:10px}}.compactViewBtn{font-size:1.1rem;border:1px solid black;padding:10px 10px;border-radius:8px;margin:10px auto;background:#fff;outline:none;box-shadow:0 5px #666;-webkit-transition:0.1s ease-in;transition:0.1s ease-in;font-weight:500}.compactViewBtn:active{box-shadow:0 2px #666;-webkit-transform:translateY(4px);transform:translateY(4px)}.compactViewBtn:hover{background-color:#fff}

.result{text-align:center}

.weekRow{background:#004181;border-bottom:1px solid black}@media (max-width: 910px){.weekRow{font-size:.8rem}.weekRow-wrapper{padding:0}.noMobile{display:none}}

.weekRow{background:#fff;border-bottom:1px solid black}.weekRow-wrapper{display:flex;text-align:center}@media only screen and (max-width: 910px){.weekRow{font-size:.8rem}.weekRow-wrapper{padding:0}}

.LabelCell{flex:1 1;height:72px;width:100%;border:none;outline-color:none;background:#fff;font-size:1rem;text-align:center;line-height:1.5;font-weight:600;overflow:hidden}@media (max-width: 910px){.LabelCell{font-size:.7rem}}.LabelCell div{height:100%;display:flex;flex-direction:column;justify-content:center}@media (max-width: 910px){.LabelCell div p{font-size:.7rem}}.noWrap p{overflow:hidden;white-space:nowrap}@media (max-width: 910px){.mobileViewHide{display:none}}.red{color:red}.green{color:green}

.weekRow-entry{flex:1 1;height:72px;color:white}.weekRow-entry input{height:50%;width:100%;font-weight:500;border:none;outline-color:black;font-size:1rem;background:#004181;text-align:center;line-height:1.5;color:white;cursor:pointer}@media (max-width: 910px){.weekRow-entry input{font-size:.7rem}}


.sideBar{display:flex;flex-direction:column;justify-content:space-around;height:100%;width:100%;color:white;overflow:hidden}

.logoimg{width:100%;height:auto;margin:25px auto}

.clock{width:100%;height:250px;text-align:center;margin:0 auto;outline:none;color:white}@media (max-width: 910px){.clock{min-height:175px;height:auto}}.clock h1{width:100%;margin:0 auto 10px;padding:0 5px;font-size:2.3rem}.clock h3{width:100%;margin:10px auto;padding:0 5px;font-size:1.1rem}.clock-hidden{visibility:hidden}

.error{width:100%;text-align:center}.error p{background-color:white;line-height:1.5;color:#f31431;font-weight:600;font-size:15px}

.saveMenu{width:100%;display:flex;flex-direction:column;margin-bottom:30px}.saveLabel{font-size:11px;color:gray}.saveMenu button{font-size:.7rem;border:1px solid black;outline:none;padding:5px;border-radius:8px;margin:5px auto;background:#fff;box-shadow:0 5px #666;font-weight:500;-webkit-transition:0.1s ease-in;transition:0.1s ease-in;width:75%;margin:1vh auto;font-size:14px;height:40px}.saveMenu button:active{box-shadow:0 2px #666;-webkit-transform:translateY(4px);transform:translateY(4px)}.saveMenu button:hover{background-color:#e6e6e6}@media (max-width: 910px){.saveMenu button{font-size:20px}}.saveMenu button[disabled]{background:#bababa}@media (max-width: 910px){.saveMenu{flex-direction:row;flex-wrap:wrap}.saveMenu button{font-size:1rem;width:45%;overflow:hidden}.saveLabel{visibility:hidden}}

.auth{width:100%;text-align:center;color:white;height:20vh}@media (max-width: 910px){.auth{height:auto}}.auth h4{font-size:1.1rem;padding-bottom:5px}.auth input{width:75%;border:none;margin:3px 0;font-size:1rem;line-height:1.5;text-align:center;outline:none}.auth .buttonWrapper{width:75%;margin:auto;display:flex;text-align:center;justify-content:center}.auth button{font-size:.7rem;border:1px solid black;outline:none;padding:5px;border-radius:8px;margin:5px auto;background:#fff;box-shadow:0 5px #666;font-weight:500;-webkit-transition:0.1s ease-in;transition:0.1s ease-in;font-weight:600;padding:3px 3px;flex:1 1;margin:3px;font-size:.7rem}.auth button:active{box-shadow:0 2px #666;-webkit-transform:translateY(4px);transform:translateY(4px)}.auth button:hover{background-color:#e6e6e6}.auth .logout-button{font-size:.7rem;border:1px solid black;outline:none;padding:5px;border-radius:8px;margin:5px auto;background:#fff;box-shadow:0 5px #666;font-weight:500;-webkit-transition:0.1s ease-in;transition:0.1s ease-in;font-weight:600;padding:10px 10px;width:50%;font-size:.7rem}.auth .logout-button:active{box-shadow:0 2px #666;-webkit-transform:translateY(4px);transform:translateY(4px)}.auth .logout-button:hover{background-color:#e6e6e6}@media (max-width: 910px){.auth .logout-button{font-size:1rem}}

.donate{text-align:center;min-height:43px}@media (max-width: 910px){.donate{margin:10px 0}}.logo{max-width:100px}

.floater{position:fixed;z-index:100;top:0;right:0;margin:5px 5px 0 0;text-align:right;text-decoration:none;color:black}.floater p{font-size:10px;font-weight:600}@media (max-width: 910px){.floater{display:none}}

.faq{background:rgba(0,0,0,0.75);height:100vh;width:100vw;position:fixed;z-index:100;display:flex}.textContent{margin:auto;height:90%;width:90%;background:white;border-radius:20px}@media (max-width: 910px){.textContent{overflow-y:scroll}}.textWrapper{padding:3vw}.questionWrapper{margin-top:20px;display:flex;flex-wrap:wrap}@media (max-width: 910px){.questionWrapper{flex-direction:column;flex-wrap:auto}}.question{width:25%;padding:10px}.question h4{font-size:1.2em;padding-bottom:5px}.question p{font-size:1em}@media (max-width: 910px){.question{width:100%}}.buttonWrapper{width:100%;text-align:center}.faq button{font-size:1.1rem;border:1px solid black;padding:10px 10px;border-radius:8px;margin:10px auto;background:#fff;outline:none;box-shadow:0 5px #666;-webkit-transition:0.1s ease-in;transition:0.1s ease-in;font-weight:500}.faq button:active{box-shadow:0 2px #666;-webkit-transform:translateY(4px);transform:translateY(4px)}.faq button:hover{background-color:#fff}

